














































































































































.stats-grid {
  display: grid;
  grid-gap: 10px;
  grid-auto-rows: minmax(110px, auto);
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
}
.stats-cont {
  &.active:after {
      position: absolute;
      content: '';
      height: 8px;
      width: 8px;
      background: #35c135;
      border-radius: 50px;
      top: 5px;
      right: 5px;
      border: 1px solid #2ab52a;
  }
}
  .goTop {
      box-shadow: 0px 0px 10px 0px #00000069;
  }
